
































































































import { Component, Vue, Watch } from "vue-property-decorator";
import { UserModule } from "@/store/modules/user";

import * as echarts from "echarts/core";
import {
    TitleComponent,
    ToolboxComponent,
    TooltipComponent,
    GridComponent,
    LegendComponent,
} from "echarts/components";
import { LineChart } from "echarts/charts";
import { UniversalTransition } from "echarts/features";
import { CanvasRenderer } from "echarts/renderers";
import { dashboard } from "@/api/other";
import moment from "moment";

echarts.use([
    TitleComponent,
    ToolboxComponent,
    TooltipComponent,
    GridComponent,
    LegendComponent,
    LineChart,
    CanvasRenderer,
    UniversalTransition,
]);

@Component({
    name: "Dashboard",
})
export default class extends Vue {
    private data: any = {};

    async created() {}

    mounted() {
        this.getDashBoard();
        // this.draw();
    }

    private async getDashBoard() {
        this.data = await dashboard();

        this.draw();
    }

    /**
     * 绘制图表
     */
    private async draw() {
        let {
            chart30: { users, vip },
        }: any = this.data;

        let days = [];
        for (let index = 30; index > -1; index--) {
            days.push(
                moment()
                    .add(0 - index, "days")
                    .format("YYYY-MM-DD")
            );
        }

        let usersDataObj: any = users.reduce((p: any, c: any) => {
            p[moment(c.date).format("YYYY-MM-DD")] = c.count;
            return p;
        }, {});

        let usersData: number[] = days.map((e: string) => {
            return usersDataObj[e] || 0;
        });

        let vipDataObj: any = vip.reduce((p: any, c: any) => {
            p[moment(c.date).format("YYYY-MM-DD")] = c.count;
            return p;
        }, {});

        let vipData: number[] = days.map((e: string) => {
            return vipDataObj[e] || 0;
        });

        // let orderDataObj: any = order.reduce((p: any, c: any) => {
        //     p[moment(c.date).format("YYYY-MM-DD")] = c.count;
        //     return p;
        // }, {});

        // let orderData: number[] = days.map((e: string) => {
        //     return orderDataObj[e] || 0;
        // });

        let chartDom: any = document.getElementById("shortTermPredict_chart");
        let myChart = echarts.init(chartDom);
        let option;

        option = {
            title: {
                text: "",
            },
            tooltip: {
                trigger: "axis",
            },
            legend: {
                data: ["注册用户", "订阅数"],
            },
            grid: {
                left: "3%",
                right: "4%",
                bottom: "3%",
                containLabel: true,
            },
            toolbox: {
                feature: {
                    saveAsImage: {},
                },
            },
            xAxis: {
                type: "category",
                boundaryGap: false,
                data: days,
            },
            yAxis: {
                type: "value",
                name: "数",
            },
            series: [
                {
                    name: "注册用户",
                    type: "line",
                    data: usersData,
                },
                {
                    name: "订阅数",
                    type: "line",
                    data: vipData,
                },
                // {
                //     name: "订单数",
                //     type: "line",
                //     data: orderData,
                // },
            ],
        };

        option && myChart.setOption(option);
    }
}
